import React from 'react';
import { Col, Image } from 'react-bootstrap';
const Category = ({ getItsProducts, cat }) => {
	return (
		<Col>
			<Col xs={6} md={4}>
				<Image
					src={cat.image}
					thumbnail
					onClick={() => {
						getItsProducts(cat.slug);
					}}
				/>
			</Col>
		</Col>
	);
};

export default Category;



